<template>
	<div>
		<van-nav-bar
		  fixed
		  border
		  title="订阅"
		  left-arrow
		  @click-left="goBack"
		/>
		<van-row type="flex" justify="center">
		  <van-col span="24" class="title">我订阅的标签</van-col>
		</van-row>
		
		<van-row type="flex" justify="center">
		  <van-col class="boxCol" span="24">
			  <van-checkbox-group  class="box" v-model="result" direction="horizontal" @change="hdchange">
			    <van-checkbox class="checkType" :name="index"  v-for="(item,index) in arr.labels" :key="index">{{item.name}}</van-checkbox>
			  </van-checkbox-group>
		  </van-col>
		</van-row>
		
		<van-row type="flex" justify="center" class="fix">
		  <van-col class="boxCol" span="12">
			  <van-button size="large" @click="goBack" type="default">取消</van-button>
		  </van-col>
		  <van-col class="boxCol" span="12">
		  		<van-button size="large" type="danger" @click="save">保存</van-button>
		  </van-col>
		</van-row>
	</div>
</template>

<script>
import { Button, NavBar, CellGroup, Cell, Field,Image , Col, Row,Checkbox, CheckboxGroup } from "vant";
import {labelList,userSetLabel,userLabels} from "@/api/v4.js"
export default{
	name: "Subscribe",
	components: {
	  [NavBar.name]: NavBar,
	  [Button.name]: Button,
	  [CellGroup.name]: CellGroup,
	  [Cell.name]: Cell,
	  [Field.name]: Field,
		[Image.name]: Image,
		[Col.name]: Col,
		[Row.name]: Row,
		[Checkbox.name]: Checkbox,
		[CheckboxGroup.name]: CheckboxGroup,
	},
	data(){
		return{
			result:[],
			currIndex:[],
			arr:[],
			selt:[]
		}
	},
	async created() {
		this.arr= await labelList();
	},
	methods:{
		goBack() {
		  if (this.from) {
		    this.$router.back();
		  } else {
		    this.$router.replace("/demands");
		  }
		},
		hdchange(val){
			this.selt = val.map(item=> this.arr.labels[item].label_id)
		},
		save(){
			console.log(this.selt)
			if(this.selt){
				userSetLabel({
					label_ids:this.selt.length>1?this.selt.join(","):this.selt
				}).then(res=>{
					// console.log(res)
					this.$dialog.alert({
						title:"温馨提示",
						message:"保存成功"
					}).then(()=>{
						this.$router.push("/user-center")
					})
				}).catch(ero=>{
					console.log(ero)
				})
			}
		}
	}
}
</script>

<style lang="less" scoped>
.fix{
	width: 100%;
	position: fixed;
	bottom: 0;
}	
.title{
	text-align: center;
	font-size: 20px;
	margin:10px 0;
}	
.checkType{
	border:1px solid #111;
	margin:5px 5px;
	padding:5px 0;
	width: 28%;
}
.box{
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
.van-checkbox__icon{
	display: block !important;
}
@media (max-width:320px) {
	.checkType{
		margin:5px 2px;
	}
}
</style>
